/** @property $errors */

/** @method isLoading */

/** @method submit */
export default {
  name: "Register",
  data: function data() {
    return {
      entity: {}
    };
  },
  beforeCreate: function beforeCreate() {
    this.$bind.mapActions('Auth', {
      isLoading: 'isLoading',
      submit: 'register'
    });
    this.$bind.mapState('Auth', {
      $errors: '$errors'
    });
  },
  beforeDestroy: function beforeDestroy() {
    this.$errors.clear();
  }
};