export default {
  name: "Login",
  data: function data() {
    return {
      user: {
        email: null,
        password: null,
        remember_me: null
      }
    };
  },
  beforeCreate: function beforeCreate() {
    this.$bind.mapState('Auth', {
      $errors: '$errors'
    });
    this.$bind.mapActions('Auth', {
      isLoading: 'isLoading',
      login: 'login'
    });
  },
  beforeDestroy: function beforeDestroy() {
    this.$errors.clear();
  }
};