export default {
  name: "PasswordReset",
  data: function data() {
    return {
      entity: {
        email: '',
        password: '',
        password_confirmation: ''
      }
    };
  },
  beforeCreate: function beforeCreate() {
    this.$bind.mapActions('Auth', {
      submit: 'forgot'
    });
    this.$bind.mapState('Auth', {
      $errors: '$errors'
    });
  },
  beforeRouteEnter: function beforeRouteEnter(to, from, next) {
    next(function (vm) {
      next(vm.$route.params.token || {
        name: 'auth.forgot'
      });
    });
  }
};