export default {
  name: "PasswordForgot",
  data: function data() {
    return {
      entity: {
        email: null,
        password: null,
        remember_me: null
      }
    };
  },
  beforeCreate: function beforeCreate() {
    this.$bind.mapActions('Auth', {
      submit: 'forgot'
    });
    this.$bind.mapState('Auth', {
      $errors: '$errors'
    });
  },
  beforeDestroy: function beforeDestroy() {
    this.$errors.clear();
  }
};